import React from 'react'
import ReactDOM from 'react-dom'
import { Link } from 'react-router-dom';
import { headers, innerHeaders, hideLoad, handleError,  showLoad, showAlert } from './../Helpers.js';
import axios from 'axios';
import serialize from 'form-serialize';
import PopupCodeEditor from './PopupCodeEditor';
import Stomp from 'stompjs';
import SockJS from 'sockjs-client';

class TerminalSocket extends React.Component {
 lastCommandSession = "lastCommand";
 /*
 socket = new WebSocket(window.location.protocol !== 'https:' ?
     `ws://${window.location.hostname}:9211/api/v2/terminal-console` :
     `wss://${window.location.hostname}/apps/webterminal/console`, 'echo-protocol');*/

 constructor(props) {
       super(props);
       this.state = {rows : [], response :  "", lastCommand:"",
                     messages: [], stompClient: null, request:{}, ws: null }
 }

 componentWillUnmount() {
    if (this.state.ws) {
          this.state.ws.close();
      }
  }
 componentDidMount(){
    this.setState({lastCommand: (sessionStorage.getItem(this.lastCommandSession) != null ?
                              sessionStorage.getItem(this.lastCommandSession):"") });
    this.useEffect();
 }



 useEffect=() => {
     let request = {};
     request.sessionUser = '252';
     request.operation   = 'connect';
     request.host        = '';
     request.port        = '';
     request.username    = '';
     request.password    = '';
     request.command     = '';


      const ws = new WebSocket('ws://localhost:9211/api/v1/terminal-console');
          ws.onopen = () => {
            console.log('WebSocket connected');
            ws.send(JSON.stringify(request));
            this.setState({ ws: ws });
          };

          ws.onmessage = (evt) => {
            const message = evt.data;
            this.setState(prevState => ({
              messages: [...prevState.messages, message]
            }));
          };

          ws.onclose = () => {
            console.log('WebSocket disconnected');
          };

          this.setState({ ws: ws, request: request });
    }



  // run Command
  runCommand =(e)=>{
     e.preventDefault();
     const form = e.currentTarget
     const body = serialize(form, {hash: true, empty: true});
     //this.state.stompClient.send(window.API_URL+'app/hello', {}, body['name']);

    this.state.request.command = body['name'];
    this.state.request.operation = 'command';
    if(this.state.ws){
       this.state.ws.send(JSON.stringify(this.state.request));
     }
  }



  render() {
    return (
       <>

         <form onSubmit={this.runCommand} action="#" method="POST">
               <div class="row shadow-sm bg-body rounded paddingBottomTopForm">
                 <div class="col-md-8">
                   <input type="text" name="name" class="form-control" required={true} defaultValue={this.state.lastCommand} placeHolder="Enter the  command, no need to enter &quot;sudo&quot; (example: apt-get -q -y install php  OR apt-get update)"/>
                 </div>
                  <div class="col-md-2">
                   <label> <input type="checkbox" name="mysql" value="yes"/> Mysql Command </label>
                 </div>
                 <div class="col-md-2">
                   <button class="btn btn-primary btn_small" type ="submit" >Run Command</button>
                 </div>
               </div>
         </form>
           <div class="height20px"></div>
           <div class="clear"></div>
           <div class="row">
               <div class="col-md-12" style={{background:"#fff",minHeight:"50vh"}}>
                   <ul style={{listStyle:"none"}}>
                     {this.state.messages.map((message, index) => (
                       <li key={index}>{message}</li>
                     ))}
                   </ul>
               </div>
            </div>

      </>
    );
  }
}

export default TerminalSocket;